*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;

  font-family: $muller;
}

a {
  transition: all 0.3s ease-in;

  svg {
    transition: all 0.3s ease-in;
  }
}

body {
  display: flex;
  flex-direction: column;

  margin: 0;

  font-size: 13px;
  font-weight: 400;
  line-height: 1.4em;

  color: $black;

  justify-content: space-between;
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4 {
  line-height: 1.2em;
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;

  overflow: hidden;
  clip: rect(0, 0, 0, 0);

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  white-space: nowrap;

  border: 0;

  clip-path: inset(100%);
}

.lg-backdrop.in {
  opacity: 0.5!important;
}

.title {
  margin-bottom: 30px;

  text-align: center;

  font-size: 25px;
  line-height: 30px;

  color: $black;

  span {
    color: $orange;
  }
}

.container {
  box-sizing: border-box;
  max-width: 1282px;
  margin: 0 auto;
  padding: 0 16px;
}

.button {
  display: block;

  width: 100%;
  padding: 14.5px;

  font-weight: 700;

  color: $white;
  border: none;
  border-radius: 6px;
  background-color: $purple;
}

input {
  padding: 10px;

  border: 1px solid $purple;
  border-radius: 6px;

  &::placeholder {
    color: $grey;
  }
}

.section {
  padding-top: 30px;
  padding-bottom: 30px;
}

.accordeon__content {
  overflow: hidden;

  max-height: 0;

  transition: all 0.8s ease-out;

  opacity: 0;
}

.accordeon__content--opened {
  max-height: 100%;

  opacity: 1;
}

#map {
  width: 100%;
}

.burger {
  position: relative;

  width: 22.5px;
  height: 17px;

  cursor: pointer;
  transition: 0.3s ease-in-out;
  transform: rotate( 0deg);
}

.burger span {
  position: absolute;
  left: 0;

  display: block;

  width: 100%;
  height: 2px;

  transition: 0.3s ease-in-out;
  transform: rotate( 0deg);

  opacity: 1;
  background: $black;
}

.burger span:nth-child(1) {
  top: 0;
}

.burger span:nth-child(2) {
  top: 7.5px;
}

.burger span:nth-child(3) {
  top: 15px;
}

.burger.open span:nth-child(1) {
  top: 7.5px;

  -webkit-transform: rotate( 135deg);
  -moz-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  transform: rotate( 135deg);
}

.burger.open span:nth-child(2) {
  left: -60px;

  opacity: 0;
}

.burger.open span:nth-child(3) {
  top: 7.5px;

  -webkit-transform: rotate( -135deg);
  -moz-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate( -135deg);
}

.overlay {
  position: fixed;
  z-index: 99;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  display: flex;

  min-width: 320px;
  padding: 30px 0;

  background: rgba(12, 11, 115, 0.2);

  justify-content: center;
  align-items: center;

  &.hidden {
    display: none;
  }
}

.swiper__button {
  position: absolute;
  z-index: 2;
  top: 50%;

  display: flex;

  width: 36px;
  height: 36px;
  padding: 0;

  cursor: pointer;
  transform: translateY(-50%);

  border: none;
  border-radius: 50%;
  background-color: white;

  align-items: center;
  justify-content: center;

  svg {
    fill: transparent;
    stroke: $black;
  }
}

.swiper-pagination {
  position: relative;
  left: 50%;

  display: flex;

  transform: translateX(-50%);

  justify-content: center;
  gap: 10px;
}

.swiper-pagination-clickable .swiper-pagination-bullet-active {
  background: $grey;
}

table {
  width: 100%;

  border-collapse: collapse;

  tr {
    &:first-of-type {
      td {
        text-transform: uppercase;

        font-weight: 500;

        background-color: #ffffff;
      }
    }
  }

  td {
    padding: 1em 10px;
  }
}

@media(min-width:600px) {
  .button {
    width: fit-content;
    padding: 19px 73px;

    font-size: 20px;
  }
}

@media(min-width:1024px) {
  .title {
    max-width: 687px;
    margin: 0 auto;
    margin-bottom: 60px;

    font-size: 40px;
    line-height: 48px;
  }

  input {
    padding: 18px 20px;

    font-size: 20px;
  }

  .section {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

@media(max-width:1400px) {
  .swiper-pagination {
    display: none;
  }
}
