.quiz {
  input {
    max-width: 467px;
  }
}

.quiz__container {
  border-radius: 10px;
  background-color: #f4f4f4;
}

.quiz__right {
  padding: 20px 15px;
}

.quiz__card {
  border-radius: 6px;
}

.quiz__step {
  &.hidden {
    display: none;
  }
}

.quiz__radio {
  position: absolute;

  opacity: 0;
}

.quiz__radio-indicator {
  display: inline-block;

  width: 10px;
  height: 10px;

  background-image: url(../img/radio.svg);
  background-repeat: no-repeat;
  background-size: contain;

  -ms-flex-negative: 0;
  flex-shrink: 0;
}

.quiz__radio:checked + .quiz__radio-indicator {
  background-image: url(../img/radio-checked.svg);
}

.quiz__steps-text {
  margin-bottom: 15px;

  text-align: center;

  font-size: 16px;
}

.quiz__line {
  overflow: hidden;

  width: 100%;
  height: 6px;
  margin-bottom: 30px;

  border-radius: 10px;
  background-color: #cacccf;
}

.quiz__progress {
  width: 0;
  height: 100%;

  background-color: $orange;
}

.quiz__card {
  display: block;
  overflow: hidden;

  border-radius: 6px;
  box-shadow: 0 15px 20px rgba(0, 0, 0, 0.05);
}

.quiz__img {
  width: 100%;
  height: 60px;

  object-fit: cover;
}

.quiz__radio-info {
  display: flex;

  min-height: 60px;
  padding: 15px 8px;

  background-color: #ffffff;

  align-items: flex-start;
  gap: 6px;
}

.quiz__text {
  word-break: break-word;

  font-weight: 500;
  line-height: 1.2em;
}

.quiz__cards {
  display: grid;

  grid-template-columns: repeat(2, 1fr);
  row-gap: 20px;
  column-gap: 10px;
}

.quiz__buttons {
  display: flex;

  gap: 10px;
}

.quiz__button {
  display: flex;

  justify-content: center;
  align-items: center;
  gap: 13px;

  svg {
    flex-shrink: 0;
    fill: none;
  }

  &[data-next] {
    svg {
      transform: rotate(-180deg);
    }
  }
}

.quiz__form {
  margin-bottom: 30px;
}

.quiz__question {
  margin-bottom: 20px;

  font-size: 16px;
  font-weight: 500;
  line-height: 1.3em;
}

.quiz__step-final {
  display: block;
}

.quiz__get-title {
  margin-bottom: 40px;

  font-size: 40px;
  font-weight: 700;

  span {
    display: block;

    color: $orange;
  }
}

.quiz__get-text {
  position: relative;

  margin-bottom: 30px;

  font-size: 32px;
  line-height: 1em;
}

.quiz__get-form {
  position: relative;

  display: grid;

  margin-bottom: 51px;

  grid-template-columns: 1fr;
  gap: 20px;
}

.quiz__get-label {
  display: flex;

  // flex-wrap: wrap;
  align-items: center;
  gap: 10px;
}

.quiz__get-contacts {
  position: relative;

  display: grid;

  margin-bottom: 50px;

  grid-template-columns: 1fr;
  gap: 20px;
}

@media(min-width:500px) {
  .quiz__img {
    height: 117px;
  }
}

@media(min-width:700px) {
  .quiz__cards {
    grid-template-columns: 1fr 1fr 1fr;
  }

  .quiz__text {
    font-size: 16px;
  }

  .quiz__radio-info {
    gap: 10px;
  }

  .quiz__radio-indicator {
    width: 18px;
    height: 18px;
  }
}

.quiz__area {
  display: flex;

  font-weight: 500;

  flex-wrap: wrap;
  align-items: center;
  gap: 10px;

  input {
    padding: 18px;

    border: 1px solid #cacccf;
    border-radius: 6px;
    outline: none;

    &:focus,
    &:active {
      border-color: $orange;
    }

    &::placeholder {
      font-weight: 400;
    }
  }
}

.quiz__error {
  margin-bottom: 1em;

  font-size: 16px;

  color: rgb(224, 33, 33);
}

@media(min-width:700px) {
  .quiz__text {
    padding-top: 3px;
  }
}

@media(max-width:1023px) {
  .quiz__number {
    display: none;
  }
}

@media(min-width:1024px) {
  .quiz__question {
    margin-bottom: 35px;

    font-size: 25px;
  }

  .quiz__area {
    font-size: 20px;

    input {
      width: 300px;
      margin-left: 14px;
    }
  }

  .quiz__right {
    padding: 50px 80px 75px 91px;
  }

  .quiz__form {
    margin-bottom: 60px;
  }

  .quiz__line {
    margin-bottom: 65px;
  }

  .quiz__container {
    display: grid;

    grid-template-columns: minmax(200px, 270px) 6fr;

    &.last {
      position: relative;

      overflow: hidden;

      grid-template-columns: 1fr;

      &::before {
        position: absolute;
        right: -30%;
        bottom: -50%;

        width: 973px;
        height: 973px;

        content: '';

        border-radius: 50%;
        background-color: $orange;
      }
    }
  }

  .quiz__cards {
    row-gap: 30px;
    column-gap: 20px;
  }

  .quiz__card {
    transition: filter 0.3s ease-in;

    &:hover,
    &:focus {
      filter: drop-shadow(0px 0px 20px rgba(76, 77, 255, 0.43));
    }
  }

  .quiz__radio-info {
    min-height: 67px;
    padding: 16px;
  }

  .quiz__buttons {
    justify-content: center;
  }

  .quiz__left {
    position: relative;

    background-size: cover;
  }

  .quiz__number {
    position: absolute;
    top: -38px;
    left: -18px;

    font-size: 524px;
    font-weight: 500;
    line-height: 1em;

    color: $white;
  }

  .quiz__steps-text {
    text-align: left;

    font-size: 25px;
    line-height: 1.3em;
  }

  .quiz__container {
    min-height: 812px;
  }

  .quiz__button {
    min-width: 206px;
  }

  .quiz__landscape {
    grid-template-columns: 1fr 1fr;
    gap: 20px;

    .quiz__card {
      display: grid;

      min-height: 138px;

      grid-template-columns: 1fr 1fr;
    }

    .quiz__img {
      height: 100%;

      order: 1;
    }

    .quiz__radio-info {
      align-items: center;
    }
  }

  .quiz__step {
    &-final {
      position: relative;

      &::before {
        position: absolute;
        right: 0;
        bottom: 0;

        width: 575px;
        height: 500px;

        content: '';

        background: url('../img/quiz-final.png');
        background-repeat: no-repeat;
      }
    }
  }
}
