.features {
  background: linear-gradient(45deg, rgba(242, 242, 242, 1) 0%, rgba(228, 236, 248, 1) 100%, rgba(248, 235, 228, 1) 100%);
}

.features__container {
  display: grid;

  grid-template-columns: 1fr;
  gap: 16px;
}

.features__item {
  display: grid;

  padding: 16px;

  border-radius: 10px;
  background: #ffffff;
  box-shadow: 0 10px 25px rgba(82, 87, 92, 0.1);

  grid-template-columns: minmax(37px, 37px) 1fr;
  gap: 20px;
}

.features__info {
  p {
    margin-bottom: 1em;
  }

  span {
    font-weight: 500;
  }
}

.features__title {
  margin-bottom: 10px;

  font-size: 20px;
  font-weight: 700;
  line-height: 1.2em;

  color: $orange;
}

@media(min-width:600px) {
  .features__container {
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }
}

@media(min-width:800px) {
  .features__item {
    padding: 34px 26px;

    grid-template-columns: 1fr;

    svg {
      width: 70px;
      height: 70px;
      margin-bottom: 30px;
    }
  }

  .features__info {
    p {
      font-size: 16px;
    }
  }

  .features__title {
    margin-bottom: 20px;

    font-size: 25px;
  }
}

@media(min-width:1200px) {
  .features__container {
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
  }
}
