.callback {
  position: relative;

  background-color: rgba(223, 203, 203, 0.377);
  background-attachment: fixed;
  background-position: 0 0;
  background-size: cover;

  &::before {
    position: absolute;

    width: 100%;
    height: 100%;

    content: '';

    background-color: rgba(25, 34, 43, 0.38);
  }

  & > .container {
    position: relative;

    padding-top: 40px;
    padding-bottom: 40px;
  }

  .title {
    margin-left: 0;

    text-align: left;

    color: $white;
  }
}

.callback__text {
  margin-bottom: 40px;

  font-size: 16px;
  font-weight: 500;

  color: $white;
}

.callback__form {
  display: grid;

  grid-template-columns: 1fr;
  gap: 20px;

  .input {
    width: 100%;
  }
}

@media(min-width:1024px) {
  .callback {
    & > .container {
      padding-top: 100px;
      padding-bottom: 100px;
    }

    .title {
      margin-bottom: 20px;
    }
  }

  .callback__text {
    margin-bottom: 60px;

    font-size: 20px;
  }

  .input {
    padding: 12px 20px;
  }

  .button {
    padding-top: 14px;
    padding-bottom: 14px;
  }
}

@media(max-width:1199px) {
  .callback__form {
    max-width: 500px;

    .label {
      display: block;

      &:last-of-type {
        margin-bottom: 20px;
      }
    }
  }
}

@media(min-width:1200px) {
  .callback__form {
    display: grid;

    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
  }
}
