.works {
  .button {
    display: none;
  }
}

.works__item {
  overflow: hidden;

  border-radius: 6px;
  background-color: rgba(223, 203, 203, 0.377);

  img {
    aspect-ratio: 220/148;
  }
}

.works__list {
  margin-bottom: 30px;
}

@media(min-width:900px) {
  .works {
    .button {
      margin: 0 auto;
    }
  }

  .works__list {
    display: grid;

    margin-bottom: 56px;

    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
  }

  .works__item {
    &:nth-child(n+9) {
      display: none;
    }
  }
}
