.footer {
  padding: 30px;

  color: $white;
  background-color: #102440;
}

.footer__container,
.footer__right {
  display: grid;

  grid-template-columns: 1fr;
  gap: 26px;
}

@media(min-width:1024px) {
  .footer {
    padding: 38px 0 61px 0;
  }

  .footer__container {
    display: flex;

    flex-wrap: wrap;
    justify-content: space-between;
  }

  .footer__right {
    gap: 23px;
  }
}
