.reviews {
  background-color: #f5f5f5;
}

.reviews__list {
  padding-bottom: 50px !important;
}

.reviews__slider {
  position: relative;
}

.reviews__card {
  box-sizing: border-box;
  padding: 42px 24px;

  border-radius: 10px;
  background-color: $white;
  box-shadow: 0 15px 20px rgba(0, 0, 0, 0.05);
}

.reviews__info {
  display: flex;

  margin-bottom: 17px;

  align-items: center;
  justify-content: space-between;
}

.reviews__name {
  font-size: 18px;

  color: $grey;
}

.reviews__time {
  font-size: 13px;
}

.reviews__stars {
  margin-bottom: 21px;
}

.reviews-left {
  left: 0;
}

.reviews-right {
  right: 0;
}

.reviews__text {
  overflow: hidden;

  max-height: 164px;
}

@media(max-width:1400px) {
  .reviews__button {
    display: none;
  }
}

@media(min-width:1100px) {
  .reviews-left {
    left: -20px;
  }

  .reviews-right {
    right: -20px;
  }
}

@media(min-width:1400px) {
  .reviews__list {
    width: 100%;
  }

  .reviews-left {
    left: -60px;
  }

  .reviews-right {
    right: -60px;
  }
}
