.header {
  z-index: 100;
}

.header__tels {
  display: grid;

  margin-bottom: 50px;

  grid-template-columns: 1fr;
  gap: 16px;
}

.header__tel {
  display: flex;

  white-space: nowrap;
  text-decoration: none;

  font-size: 25px;
  font-weight: 500;
  line-height: 1em;

  color: $orange;

  flex-wrap: wrap;
  gap: 10px;
}

.header__nav {
  position: absolute;
  z-index: 1;
  left: 0;

  width: 100%;
  padding: 10px 20px;
  padding-bottom: 50px;

  transition: opacity 0.3s ease-in;

  opacity: 1;
  background-color: #ffffff;
}

.header__logo {
  display: block;

  width: 188px;

  svg {
    width: 100%;
    height: auto;
  }
}

.header__logo-container {
  display: flex;

  padding: 16px 0;

  align-items: center;
  justify-content: space-between;
}

.header__burger {
  display: flex;

  width: 40px;
  height: 40px;

  border: none;
  border-radius: 50%;
  background: transparent;
  background-color: $purple;

  align-items: center;
  justify-content: center;

  .burger {
    span {
      background-color: #ffffff;
    }
  }
}

@media(max-width:1023px) {
  .header__nav {
    &.hidden {
      z-index: -1;
      left: -100%;

      opacity: 0;
    }
  }

  .header > .container {
    padding: 0;
  }

  .header__logo-container {
    padding-right: 16px;
    padding-left: 16px;

    transition: all 0.3s ease-in;

    &.active {
      z-index: 100;

      background-color: $white;
    }
  }
}

@media(min-width:1024px) {
  .header {
    padding: 17px 0;

    & > .container {
      display: flex;

      align-items: center;
      gap: 30px;
    }

    button {
      min-width: 187px;
      padding: 15px 33px;

      font-size: 16px;
    }
  }

  .header__logo-container {
    padding: 0;
  }

  .header__logo {
    width: 255px;
  }

  .header__burger {
    display: none;
  }

  .header__tels {
    margin-bottom: 0;

    gap: 4px;
  }

  .header__tel {
    font-size: 16px;

    svg {
      display: none;
    }
  }

  .header__nav {
    position: static;

    display: grid;

    padding-bottom: 0;

    background-color: transparent;

    align-items: center;
    grid-template-columns: 3fr 1fr 1fr;
    column-gap: 32px;
    row-gap: 10px;
  }
}
