.card {
  position: relative;
}

.card__container {
  margin: 0 auto;
  padding: 0;
}

.card__heading {
  margin: 0;
  margin-bottom: 10px;

  font-weight: 400;

  color: $black;
}

.card__info {
  max-width: 625px;
}

.card__swiper {
  display: flex;

  margin-bottom: 30px;

  flex-wrap: wrap;
}

.card__previews {
  width: 100%;
}

.card__slider {
  width: 100%;
  height: auto;

  border-radius: 10px;
}

.card__preview {
  position: relative;

  display: flex;
  overflow: hidden;

  cursor: pointer;

  border-radius: 10px;
  background-color: rgba(223, 203, 203, 0.377);

  justify-content: center;
}

.card__heading {
  width: 100%;

  font-size: 26px;
  line-height: 33px;

  color: $black;
}

.card__slider {
  margin-bottom: 20px;
}

.card__slide {
  display: flex;

  background-color: rgba(0, 0, 0, 0.1);

  align-items: center;
  justify-content: center;
}

.card__slide-img {
  width: 100%;
  height: auto;

  object-fit: cover;
}

.card__button {
  width: 100%;
  margin-bottom: 60px;
  padding: 16.5px;
}

.card__text {
  span {
    display: block;

    margin-bottom: 10px;

    font-size: 12px;
    line-height: 16px;
  }

  &.container {
    max-width: 600px;
  }
}

.card__video {
  display: none;
}

.card__video-heading {
  margin-bottom: 30px;

  font-size: 24px;
  line-height: 31px;
}

.card__previews {
  margin-bottom: 25px;
}

.card__p {
  margin-bottom: 10px;

  font-size: 16px;
}

.card__preview-img {
  object-fit: cover;
}

.card__title {
  margin-bottom: 46px;

  font-size: 20px;
  line-height: 1.3em;
}

.card__table {
  margin-bottom: 40px;

  td {
    &:last-of-type {
      text-align: right;
    }
  }

  tr {
    &:last-of-type {
      td {
        text-transform: uppercase;

        font-weight: 500;

        background-color: #ffffff;
      }
    }

    &.discount {
      td {
        color: $orange;
      }
    }
  }
}

@media(max-width:1069px) {
  .card__slider {
    max-height: 300px;
  }
}

@media(min-width:1070px) {
  .card__container {
    display: flex;

    justify-content: space-between;
    align-items: flex-start;
  }

  .card__swiper {
    width: 516px;
    margin-right: 60px;
    margin-bottom: 0;
    padding-top: 0;
  }

  .card__description {
    display: flex;

    flex-grow: 1;
  }

  .card__previews {
    margin-bottom: 56px;
  }

  .card__heading {
    margin: 0;
    margin-bottom: 40px;
    padding: 0;

    font-size: 38px;
    line-height: 49px;
  }

  .card__p {
    margin-bottom: 20px;

    font-size: 20px;
  }

  .card__text {
    span {
      margin-bottom: 20px;

      font-size: 20px;
      line-height: 130%;
    }

    &.container {
      max-width: 1110px;
    }
  }

  .card__previews {
    height: 85px;
  }

  .card__video {
    display: block;
  }
}

@media(max-width:1069px) {
  .card__previews {
    display: none;
  }
}

@media(min-width:1070px) {
  .card__title {
    font-size: 25px;
  }

  .card__table {
    font-size: 16px;
  }

  .card__container {
    margin-bottom: 85px;
  }
}
