.help {
  width: 100%;
  margin-bottom: 50px;

  .title {
    margin-left: 0;

    text-align: left;
  }
}

.help__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.help__question {
  position: relative;

  padding: 27px;
  padding-left: 60px;

  cursor: pointer;
  transition: font-size 1s ease-out, line-height 1s ease-out;

  font-size: 16px;
  font-weight: 700;
  line-height: 24px;

  // &:hover,
  // &:focus {
  //   color: $white;
  //   background-color: $orange;
  // }
  &:last-of-type {
    margin-bottom: 0;
  }

  &::before {
    position: absolute;
    top: 50%;
    left: 0;

    width: 40px;
    height: 40px;

    content: '';
    transform: translateY(-50%);

    font-size: 50px;
    font-weight: 500;

    color: $orange;
    background: url('../img/plus.svg');
  }

  &.accordeon__button--opened {
    margin-bottom: 0;

    font-size: 18px;
    line-height: 34px;

    border-bottom: 1px solid #4c4dff;

    &::before {
      content: '';

      color: $white;
      background: url('../img/minus.svg');
    }
  }
}

.help__item {
  border-top: 1px solid #4c4dff;

  &:last-of-type {
    border-bottom: 1px solid #4c4dff;
  }
}

.help__answer {
  line-height: 21px;

  p {
    margin-bottom: 1em;

    line-height: 1.3em;
  }
}

.help__answer.accordeon__content {
  padding: 0 20px;
}

.help__answer.accordeon__content--opened {
  padding: 20px 0;
}

.help__text {
  margin-bottom: 40px;

  font-size: 16px;
}

@media(min-width: $widthDesktop) {
  .help {
    margin-bottom: 50px;
  }

  .help__text {
    margin-bottom: 50px;

    font-size: 20px;
  }

  .help__question {
    padding: 29px;
    padding-left: 60px;

    font-size: 25px;
    line-height: 1.3em;

    &.accordeon__button--opened {
      font-size: 29px;
      line-height: 35px;
    }
  }

  .help__answer.accordeon__content--opened {
    padding: 30px 0 50px 0;

    font-size: 27px;
  }
}
