.nav {
  display: grid;

  margin-bottom: 40px;
  padding-top: 20px;

  gap: 27px;
}

.nav__link {
  display: block;

  text-decoration: none;

  font-size: 20px;
  font-weight: 500;

  color: $black;

  &:last-of-type {
    padding-right: 0;
  }

  &:first-of-type {
    padding-left: 0;
  }
}

@media(min-width:1024px) {
  .nav {
    display: flex;

    width: 100%;
    margin-bottom: 0;
    padding-top: 0;

    justify-content: space-around;
    flex-wrap: wrap;
    gap: 6px;
  }

  .nav__link {
    padding: 5px 10px;

    font-size: 16px;
  }
}
