.modal {
  &--closed {
    display: none;
  }

  .form {
    display: grid;

    grid-template-columns: 1fr;
    gap: 20px;
  }

  input {
    width: 100%;
    padding: 12px 14px;
  }

  label {
    display: block;

    &:last-of-type {
      margin-bottom: 20px;
    }
  }
}

@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }

  70% {
    transform: translateY(30px);
  }

  90% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0);
  }
}

.modal__container {
  position: absolute;

  width: 100%;
  max-width: 680px;
  margin: 0 auto;
  padding: 32px;
  z-index: 3;

  animation: bounce 1s;

  background-color: $white;

  flex-shrink: 0;
}

.modal__info {
  margin-bottom: 30px;

  font-size: 16px;
  line-height: 1.3em;
}

.modal {
  .title {
    color: $black;
  }
}

.modal__close {
  display: flex;

  width: 40px;
  height: 40px;
  margin-bottom: 10px;
  margin-left: auto;
  padding: 0;

  border: none;
  border-radius: 50%;
  background-color: #e8e8e8;

  align-items: center;
  justify-content: center;
}

@media(min-width:1024px) {
  .modal__container {
    padding: 70px 30px;
  }

  .modal__close {
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .modal__info {
    font-size: 20px;
  }
}
