.contacts__container {
  display: grid;

  grid-template-columns: 1fr;
  gap: 20px;
}

.contacts__title {
  margin-bottom: 8px;

  font-size: 30px;
  line-height: 1.3em;

  color: $white;
}

.contacts__map {
  width: 100%;
  height: 420px;
}

.contacts__items {
  display: grid;

  grid-template-columns: 1fr;
  gap: 16px;
}

.contacts__info {
  padding: 30px 20px;

  color: $white;
  border-radius: 6px;
  background-color: $purple;
}

.contacts__items {
  margin-bottom: 30px;
}

.contacts__item {
  display: grid;

  grid-template-columns: minmax(20px, 20px) 1fr;
  gap: 16px;
}

.contacts__link {
  display: block;

  text-decoration: none;

  line-height: 1.6em;

  color: $white;
}

.contacts__text {
  margin-bottom: 20px;

  font-size: 16px;
}

.contacts__subtitle {
  margin-bottom: 26px;

  font-size: 20px;
}

.contacts__socials {
  display: flex;

  flex-wrap: wrap;
  gap: 20px;
}

@media(min-width:900px) {
  .contacts__container {
    grid-template-columns: 1fr minmax(100px, 550px);
    gap: 30px;
  }

  .contacts__info {
    order: 1;
  }
}

@media(min-width:1024px) {
  .contacts__map {
    height: 701px;
  }

  .contacts__info {
    padding: 60px 52px;
  }

  .contacts__title {
    font-size: 50px;
  }

  .contacts__text {
    margin-bottom: 26px;

    font-size: 20px;
    line-height: 1.3em;
  }

  .contacts__link,
  .contacts__item {
    font-size: 20px;
  }

  .contacts__items {
    margin-bottom: 40px;

    gap: 26px;
  }
}
