.examples {
  background-color: rgba(244, 244, 244, 1);

  .title {
    margin: 0;
    margin-bottom: 30px;

    text-align: left;
  }
}

.examples-left {
  left: 0;
}

.examples-right {
  right: 0;
}

@media(max-width:1069px) {
  .card__previews-wrapper.swiper-wrapper {
    display: none;
  }
}

@media (max-width: 1400px) {
  .examples-left,
  .examples-right {
    display: none;
  }

  .examples__buttons {
    display: flex;

    align-items: center;
    justify-content: flex-end;
    gap: 15px;

    .swiper__button {
      position: static;

      display: block;

      transform: none;
    }
  }

  .examples-slider {
    margin-bottom: 40px;
  }
}

@media (min-width: 1400px) {
  .examples-left {
    left: -60px;
  }

  .examples-right {
    right: -60px;
  }
}
