.banner {
  overflow: hidden;

  height: 510px;

  .container {
    position: relative;

    width: 100%;
    height: 100%;
  }
}

.banner__container {
  position: absolute;
  top: 0;

  width: 100%;
  height: 579px;

  background-color: rgb(182, 169, 169);
  background-repeat: no-repeat;
  // background-attachment: fixed;
  background-position: 70% center;
  background-size: cover;
}

.banner__card {
  position: absolute;
  top: 140px;
  left: 50%;

  width: calc(100% - 30px);
  padding: 40px 16px;

  transform: translateX(-50%);

  border-radius: 10px;
  background: rgba(255, 255, 255, 0.61);
}

.banner__title {
  margin: 0;
  margin-bottom: 12px;

  text-align: center;

  font-size: 30px;
  font-weight: 700;
  line-height: 1.3em;
}

.banner__text {
  margin-bottom: 40px;

  text-align: center;

  font-size: 20px;
  font-weight: 500;
  line-height: 1.3em;
}

.banner__button {
  width: 100%;
  padding: 11px 40px;

  font-size: 16px;

  color: $white;
  border: none;
  border-radius: 6px;
  background-color: $orange;
}

@media(min-width:650px) {
  .banner__card {
    max-width: 546px;
  }
}

@media(min-width:1100px) {
  .banner {
    height: 720px;
  }

  .banner__container {
    height: 800px;

    background-position: center center;
  }

  .banner__card {
    position: relative;
    top: 210px;
    left: 0;

    padding: 50px;

    transform: none;
  }

  .banner__title {
    margin-bottom: 20px;

    font-size: 50px;
  }

  .banner__text {
    margin-bottom: 40px;

    font-size: 20px;
  }

  .banner__button {
    padding: 11px 50px;

    font-size: 20px;
  }
}
